.two-col-promo {
	text-align: center;

	& .row {
		margin: 0;
	}

	& h3 {
		font-size: 25px;
		color: #fff;
		padding: 0;
		text-transform: uppercase;
		overflow: hidden;
		width: 0;
		margin: 0px auto;
		white-space: nowrap;

		@media only screen and (max-width: 767px) {
			& {
				font-size: 13px;
			}
		}
	}

	& [class^="col-"] {
		padding: 75px 0;

		@media only screen and (max-width: 767px) {
			& {
				padding: 50px 0;
			}
		}
	}

	& .left-col {
		background: #3078BF;

		& h3.typeIn {
			width: auto;
			animation: typewritter 5s steps(30, end);
		}
	}

	& .right-col {
		background: #83b9ff;

		& h3.typeIn {
			width: auto;
			animation: typewritter2 5s steps(30, end);
		}
	}
}