.latest-post {
	& .list-group {
		& .list-group-item {
			border: 0;
			border-bottom: 1px solid #F1f2f3;
			margin: 0;
			padding: 15px 0;

			& .post {


				& .post_img {
					width: 75px;
					height: 75px;
					padding: 1px;
					border: 1px solid #ccc;
					float: left;
					margin-right: 10px;
					overflow: hidden;

					& img {
						width: 100%;
						height: 100%;
					}
				}

				& .post_content {
					width: calc(100% - 85px);
					float: left;

					& h5 {
						padding: 0;
						color: #333;
						font-size: 16px;
						display: inline;
					}

					& p {
						margin: 10px 0 0 0;
						color: #333;
						line-height: 17px;
					}
				}
			}

			&:hover {
				h5 {
					text-decoration: none;
					border-bottom: 1px solid #ccc;
					color: #007bff;
				}
			}
		}
	}
}